/*
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
 *
 *  overwrite bootstrap
 *
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
*/
body {
    font-family: "PingFangSC-Light";
    background-color: #eceff0;
}

/*a {*/
    /*color: #666;*/
/*}*/

/*a:hover {*/
    /*color: #2479CC;*/
/*}*/

a:hover,
a:focus {
    text-decoration: none;
}

/*
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
 *
 * main
 *
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
*/
html {
    overflow: -moz-scrollbars-vertical;
    overflow-y: scroll;
}

.panel {
    /*border-radius: 0;*/

    -webkit-box-shadow: 0 1px 4px 0 rgba(0,0,0,0.14);
    box-shadow: 0 1px 4px 0 rgba(0,0,0,0.14);
    margin-bottom: 24px;
}

@font-face {
    font-family: scriptin;
    src: url("//blog.azlar.cc/ef99bd98798f63075208ad127c843b8c.ttf");
}

/*
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
 *
 *  app - container
 *
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
*/
.blog {
    color: #666;
    max-width: 90%;
    margin: 0 auto;
    text-rendering: geometricPrecision;
}


/*
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
 *
 *  header
 *
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
*/
.blog .header-panel {
    width: 100%;
    height: 108px;
    padding: 0 1em;

    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
}

.blog .header-panel > div {
    display: inline-block;
    vertical-align: top;
}

.blog .header-panel .logo {
    margin-top: 18px;
}

.blog .header-panel .logo .avatar {
    width: 72px;
    height: 72px;
    background-size: cover;
    background-position: 78%;
    border-radius: 100px;
}

.blog .header-panel .blog-name {
    /*margin-top: 2em;*/
    margin-left: 5em;
    font-size: 1.5em;

    height: 100%;
}


.blog .header-panel .menus {
    float: right;
    font-size: 1.5em;
    height: 100%;
}

.blog .header-panel .blog-name:before,
.blog .header-panel .menus:before {
    content: '';
    display: inline-block;
    vertical-align: middle;
    height: 100%;
}



.blog .header-panel .menus a {
    color: #666;
    display: inline-block;
    padding: 0 0.5em;
    transition: color 0.7s;
    text-decoration: none;
}

.blog .header-panel .menus a:hover {
    color: #2479CC;
}

/* mini - header */
.blog.read .header-panel,
.blog.fixed .header-panel {
    height: 70px;
}

.blog.read .header-panel > div,
.blog.fixed .header-panel > div {
    /*display: none;*/
}

.blog.read .header-panel .logo,
.blog.fixed .header-panel .logo {
    margin-top: 10px;
}

.blog.read .header-panel .logo .avatar,
.blog.fixed .header-panel .logo .avatar {
    width: 50px;
    height: 50px;
    background-size: cover;
    background-position: 78%;
    border-radius: 100px;
}

.blog.read .header-panel .blog-name,
.blog.fixed .header-panel .blog-name {
    font-size: 1em;
}

.blog.read .header-panel .menus,
.blog.fixed .header-panel .menus {
    font-size: 1em;
}

/* transition */
.blog .header-panel {
    -webkit-transition: height 0.7s;
    -moz-transition: height 0.7s;
    -ms-transition: height 0.7s;
    -o-transition: height 0.7s;
    transition: height 0.7s;
}

.blog .header-panel .logo {
    -webkit-transition: margin-top 0.7s;
    -moz-transition: margin-top 0.7s;
    -ms-transition: margin-top 0.7s;
    -o-transition: margin-top 0.7s;
    transition: margin-top 0.7s;
}

.blog .header-panel .logo .avatar {
    -webkit-transition: height 0.7s, width 0.7s;
    -moz-transition: height 0.7s, width 0.7s;
    -ms-transition: height 0.7s, width 0.7s;
    -o-transition: height 0.7s, width 0.7s;
    transition: height 0.7s, width 0.7s;
}

.blog .header-panel .blog-name
{
    -webkit-transition: margin 0.7s, font-size 0.7s;
    -moz-transition: margin 0.7s, font-size 0.7s;
    -ms-transition: margin 0.7s, font-size 0.7s;
    -o-transition: margin 0.7s, font-size 0.7s;
    transition: margin 0.7s, font-size 0.7s;
}

.blog .header-panel .menus {
    -webkit-transition: margin 0.7s, font-size 0.7s;
    -moz-transition: margin 0.7s, font-size 0.7s;
    -ms-transition: margin 0.7s, font-size 0.7s;
    -o-transition: margin 0.7s, font-size 0.7s;
    transition: margin 0.7s, font-size 0.7s;
}


/*
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
 *
 *  widget
 *
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
*/
.blog .widget-panel {
    float: left;
    /*min-height: calc(100% - 156px);*/

    height: calc(100vh - 156px);
    width: calc(100vw * 0.22 * 0.9 - 3px);

    margin-top: 132px;
    position: fixed;

    /*overflow-x: hidden;*/
    /*overflow-y: auto;*/

    -webkit-transition: transform 0.7s, width 0.7s, min-height 0.7s;
    -moz-transition: transform 0.7s, width 0.7s, min-height 0.7s;
    -ms-transition: transform 0.7s, width 0.7s, min-height 0.7s;
    -o-transition: transform 0.7s, width 0.7s, min-height 0.7s;
    transition: transform 0.7s, width 0.7s, min-height 0.7s;
}

.blog .widget-panel > div {
    -webkit-transition: font-size 0.7s, opacity 0.7s, visibility 0.7s;
    -moz-transition: font-size 0.7s, opacity 0.7s, visibility 0.7s;
    -ms-transition: font-size 0.7s, opacity 0.7s, visibility 0.7s;
    -o-transition: font-size 0.7s, opacity 0.7s, visibility 0.7s;
    transition: font-size 0.7s, opacity 0.7s, visibility 0.7s;

    overflow-x: hidden;
    overflow-y: auto;
    height: calc(100% - 53px);
}
.blog .widget-panel > div::-webkit-scrollbar {
    width: 0;
    height: 0;
    background: transparent;
}

.blog .widget-panel > title.head {
    display: block;
    text-align: center;
    font-size: 22px;
    border-bottom: 1px solid #eee;
    padding: 12px 0;
    /*border-radius: 0;*/
    line-height: 28px;
    border-left: 3px solid #337ab7;
    border-top-left-radius: 3px;
    -webkit-transition: font-size 0.7s, opacity 0.7s, visibility 0.7s;
    -moz-transition: font-size 0.7s, opacity 0.7s, visibility 0.7s;
    -ms-transition: font-size 0.7s, opacity 0.7s, visibility 0.7s;
    -o-transition: font-size 0.7s, opacity 0.7s, visibility 0.7s;
    transition: font-size 0.7s, opacity 0.7s, visibility 0.7s;
}

.blog .widget-panel > span.msg {
    font-family: 'scriptin';
    display: block;
    text-align: center;
    padding-top: calc(100% - 50% + 11px);
    font-size: 22px;
    font-weight: bold;

    -webkit-transition: font-size 0.7s, opacity 0.7s, visibility 0.7s;
    -moz-transition: font-size 0.7s, opacity 0.7s, visibility 0.7s;
    -ms-transition: font-size 0.7s, opacity 0.7s, visibility 0.7s;
    -o-transition: font-size 0.7s, opacity 0.7s, visibility 0.7s;
    transition: font-size 0.7s, opacity 0.7s, visibility 0.7s;
}

/* read */
.blog.read .widget-panel {
    width: 0 !important;
    /*opacity: 0;*/

    -webkit-transform: translate(-5vw, 0);
    -moz-transform: translate(-5vw, 0);
    -ms-transform: translate(-5vw, 0);
    -o-transform: translate(-5vw, 0);
    transform: translate(-5vw, 0);

    -webkit-transition: transform 0.7s, width 0.7s, min-height 0.7s;
    -moz-transition: transform 0.7s, width 0.7s, min-height 0.7s;
    -ms-transition: transform 0.7s, width 0.7s, min-height 0.7s;
    -o-transition: transform 0.7s, width 0.7s, min-height 0.7s;
    transition: transform 0.7s, width 0.7s, min-height 0.7s;

    -webkit-transition-delay: 0.1s;
    -moz-transition-delay: 0.1s;
    -ms-transition-delay: 0.1s;
    -o-transition-delay: 0.1s;
    transition-delay: 0.1s;

}

.blog.fixed .widget-panel {
    /*margin-top: 94px;*/
    min-height: calc(100% - 118px);

    -webkit-transform: translate(0, -38px);
    -moz-transform: translate(0, -38px);
    -ms-transform: translate(0, -38px);
    -o-transform: translate(0, -38px);
    transform: translate(0, -38px);

    -webkit-transition: transform 0.7s, width 0.7s, min-height 0.7s;
    -moz-transition: transform 0.7s, width 0.7s, min-height 0.7s;
    -ms-transition: transform 0.7s, width 0.7s, min-height 0.7s;
    -o-transition: transform 0.7s, width 0.7s, min-height 0.7s;
    transition: transform 0.7s, width 0.7s, min-height 0.7s;
}

.blog.read .widget-panel > div {
    font-size: 0;
    opacity: 0;
    visibility: hidden;
}

.blog.read .widget-panel > title.head {
    font-size: 0;
    opacity: 0;
    visibility: hidden;
}

.blog.read .widget-panel > span.msg {
    font-size: 0;
    opacity: 0;
    visibility: hidden;
}


