/*
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
 *
 *  wrapper
 *
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
*/
.blog .wrapper {
    width: 78%;
    float: left;
    margin-top: 132px;
    margin-left: 22%;

    -webkit-transition: transform 0.7s;
    -moz-transition: transform 0.7s;
    -ms-transition: transform 0.7s;
    -o-transition: transform 0.7s;
    transition: transform 0.7s;

    -webkit-transition-delay: 0.1s;
    -moz-transition-delay: 0.1s;
    -ms-transition-delay: 0.1s;
    -o-transition-delay: 0.1s;
    transition-delay: 0.1s;

    /*-webkit-transition-delay: -0.2s;*/
    /*-moz-transition-delay: -0.2s;*/
    /*-ms-transition-delay: -0.2s;*/
    /*-o-transition-delay: -0.2s;*/
    /*transition-delay: -0.2s;*/
}

.blog .wrapper > div:first-child {
    margin-left: 24px;
}

.blog.read .wrapper {
    /*margin-left: 11%;*/

    transform: translate(-11vw, 0);

    -webkit-transition-delay: 0s;
    -moz-transition-delay: 0s;
    -ms-transition-delay: 0s;
    -o-transition-delay: 0s;
    transition-delay: 0s;
}

/*
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
 *
 *  main - content
 *
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
*/
.blog .wrapper .content-box {
    width: 100%;
    margin: 2em 0;
    padding: 0 20px 20px 20px;
    background-color: #fff;
}

.blog .wrapper .content-box .title {

}

.blog .wrapper .mark-down-menu {
    display: inline-block;
    border: 1px solid #eee;
    padding: 0.5em 0 0.5em 0;
    float: right;
    margin: 0 0 1em 1.1em;
    max-width: 37%;
    overflow: hidden;
    z-index: 100;
    background-color: #fff !important;
    box-shadow: 3px 3px 16px 0 rgba(0,0,0,0.16);

    position: relative;
}

.blog .wrapper .mark-down-menu .name {
    padding: 1em;
    font-size: 1.1em;
    font-weight: 700;
    border-bottom: 1px solid #eee;
}

.blog .wrapper .mark-down-menu ul {
    position: relative;
    z-index: 100;
}

.blog .wrapper .mark-down-menu ul > li {
    margin-right: 2em;
    color: #337ab7;
}

.blog .wrapper .mark-down-menu li.first {
    font-weight: bolder;
    list-style-type: cjk-ideographic;
}

.blog .wrapper .mark-down-menu li.first-end:after {
    content: '';
    display: block;
    border-bottom: 1px solid #eee;
    margin: 1em 0 1em -2.9em;
}

.blog .wrapper .mark-down-menu li.third {
    list-style-type: circle;
    margin-left: 2em;
}

.blog .wrapper .content .desc {
    word-break: break-all;
}

.blog .wrapper .content *[id]:before {
    /*display: block;*/
    /*content: "";*/
    /*margin-top: -82px;*/
    /*height: 82px;*/
    /*visibility: hidden;*/

    display: inline-block;
    content: "";
}

.blog .wrapper .content *[id] {
    padding-top: 0 !important;

    /* for the <pre> tag scroll, add z-index to the titles */
    position: relative;
    z-index: 0;
}

.blog .wrapper .content *[id].selected {
    color: #666;
    animation: a-selected-change 3s;
    -moz-animation: a-selected-change 3s;
    -webkit-animation: a-selected-change 3s;
}

.blog .wrapper .content ul {
    position: relative;
    z-index: 10;
}

@-moz-keyframes a-selected-change
{
    0% {color: #666;}
    75% {color: #337ab7; }
    100% {color: #666;}
}

@-webkit-keyframes a-selected-change
{
    0% {color: #666;}
    75% {color: #337ab7; }
    100% {color: #666;}
}

/* at first, I add this for <ul>;now I add to every <tag> except [id](head)  */
.blog .wrapper .content *:not([id]).selected {
    background-color: transparent;
    animation: ul-selected-change 3s;
    -moz-animation: ul-selected-change 3s;
    -webkit-animation: ul-selected-change 3s;
}

@-moz-keyframes ul-selected-change
{
    0% {
        background-color: transparent;
        transform: scale(1, 1);
    }
    50% {
        background-color: rgba(255, 255, 152, 0.7);
        transform: scale(1.05, 1);
    }
    100% {
        background-color: transparent;
        transform: scale(1, 1);
    }
}

@-webkit-keyframes ul-selected-change
{
    0% {
        background-color: transparent;
        transform: scale(1, 1);
    }
    50% {
        background-color: rgba(255, 255, 152, 0.7);
        transform: scale(1.05, 1);
    }
    100% {
        background-color: transparent;
        transform: scale(1, 1);
    }
}


/* to top */
.blog .to-top-box {
    /*display: none;*/
    visibility: hidden;
    position: fixed;
    bottom: 20%;
    right: 0;
    background-color: #fff;

    opacity: 0;

    -webkit-transition: right 0.7s, opacity 0.7s, visibility 0.7s;
    -moz-transition: right 0.7s, opacity 0.7s, visibility 0.7s;
    -ms-transition: right 0.7s, opacity 0.7s, visibility 0.7s;
    -o-transition: right 0.7s, opacity 0.7s, visibility 0.7s;
    transition: right 0.7s, opacity 0.7s, visibility 0.7s;

    /*-webkit-transition-delay: 0.1s;*/
    /*-moz-transition-delay: 0.1s;*/
    /*-ms-transition-delay: 0.1s;*/
    /*-o-transition-delay: 0.1s;*/
    /*transition-delay: 0.1s;*/
}

.blog.read .to-top-box {
    right: 10vw;

    -webkit-transition-delay: 0s;
    -moz-transition-delay: 0s;
    -ms-transition-delay: 0s;
    -o-transition-delay: 0s;
    transition-delay: 0s;
}

.blog.fixed .to-top-box {
    visibility: visible;
    /*right: 0;*/
    opacity: 1;
}

.blog.read .to-top-box {
    visibility: visible;
    opacity: 1;
}

.blog .to-top-box a {
    display: block;
    width: 55px;
    height: 55px;
    padding: 1em;
    border: 1px solid #38b7ea;
}

.blog .to-top-box a svg {
    color: #38b7ea;
    fill: currentColor;
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
}

.blog .to-top-box a svg:hover {
    animation: to-top-change 0.3s linear 2;
    -moz-animation: to-top-change 0.3s linear 2;
    -webkit-animation: to-top-change 0.3s linear 2;
}

@-moz-keyframes to-top-change {
    from{
        transform: rotate(-45deg);
        -webkit-transform: rotate(-45deg);
        -moz-transform: rotate(-45deg);
    }
    to{
        transform: rotate(-405deg);
        -webkit-transform: rotate(-405deg);
        -moz-transform: rotate(-405deg);
    }
}

@-webkit-keyframes to-top-change {
    from{
        transform: rotate(-45deg);
        -webkit-transform: rotate(-45deg);
        -moz-transform: rotate(-45deg);
    }
    to{
        transform: rotate(-405deg);
        -webkit-transform: rotate(-405deg);
        -moz-transform: rotate(-405deg);
    }
}


/* comment box */
.blog .comments-box {
    border-top: 1px solid #eee;
    margin-top: 50px;
    padding-top: 50px;
}


/* list - page */
.blog .list-panel {
    color: #666;
}

.blog .list-panel .article {
    min-height: 60px;
    padding: 7px;
}

.blog .list-panel .article .name {
    margin: 10px 0 5px 0;
}

.blog .list-panel .pager {
    padding: 0;
    text-align: center;
    margin-bottom: 24px;
}

.blog .list-panel .pager li {
    display: inline-block;
}

.blog .list-panel .pager li a {
    display: block;
    background-color: #fff;
    padding: 7px 10px;
    border-radius: 4px;
    box-shadow: 0 1px 4px 0 rgba(0,0,0,0.14);
}

.blog .list-panel .pager li a.disabled {
    color: #777;
    cursor: not-allowed;
}

.blog .list-panel .pager li:last-child {
    margin-left: 80px;
}

.blog .list-panel .pager li a:not(.disabled):hover {
    animation: shake 0.25s 1;
}
@keyframes shake {
    10% {
        transform: translate(-13px, 0);
    }
    20% {
        transform: translate(0px, 0);
    }
    30% {
        transform: translate(13px, 0);
    }
    40% {
        transform: translate(0px, 0);
    }
    50% {
        transform: translate(13px, 0);
    }
    60% {
        transform: translate(13px, 0);
    }
    70% {
        transform: translate(5px, 0);
    }
    80% {
        transform: translate(-6px, 0);
    }
    90% {
        transform: translate(13px, 0);
    }
    0%, 100% {
        transform: translate(0, 0);
    }
}



/* some article tag style */


/*
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
 *
 * tags
 * &
 * article-box.widget
 *
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
*/

.blog .tag-box.block,
.blog .article-box.widget {
    /*font-size: 1.3em;*/
}

.blog .tag-box .tag-string {
    color: #999;
    -webkit-transition: color 0.4s, font-size 0.4s;
    -moz-transition: color 0.4s, font-size 0.4s;
    -ms-transition: color 0.4s, font-size 0.4s;
    -o-transition: color 0.4s, font-size 0.4s;
    transition: color 0.4s, font-size 0.4s;
}

.blog .tag-box .tag-string:not(:last-child):after {
    content: ', ';
    color: #999;
}

.blog .tag-box .tag-string:hover {
    color: #337ab7;
    font-size: 1.15em;
}

.blog .tag-box .tag-string.selected {
    color: #777;
    font-size: 1.15em;
}

/*.blog .tag-box .head {*/
    /*text-align: center;*/
    /*font-size: 1.5em;*/
    /*border-bottom: 1px solid #eee;*/
    /*padding: 12px 0;*/
    /*border-radius: 0;*/
    /*border-left: 3px solid #337ab7;*/
/*}*/

.blog .tag-box .tag-block,
.blog .article-box.widget .article-block {
    display: block;
    line-height: 28px;
    border-bottom: 1px dotted rgba(0,0,0,0.1);
    padding: 7px;

    /*height: 43px;*/
    /*white-space: nowrap;*/

    color: #666;
    background-color: #fff;

    -webkit-transition: background-color 0.2s, color 0.2s;
    -moz-transition: background-color 0.2s, color 0.2s;
    -ms-transition: background-color 0.2s, color 0.2s;
    -o-transition: background-color 0.2s, color 0.2s;
    transition: background-color 0.2s, color 0.2s;
}

.blog .tag-box .tag-block:hover,
.blog .tag-box .tag-block.selected,
.blog .article-box.widget .article-block:hover,
.blog .article-box.widget .article-block.selected {
    background-color: #38B7EA;
    color: #fff;

    -webkit-transition: background-color 0.1s, color 0.1s;
    -moz-transition: background-color 0.1s, color 0.1s;
    -ms-transition: background-color 0.1s, color 0.1s;
    -o-transition: background-color 0.1s, color 0.1s;
    transition: background-color 0.1s, color 0.1s;
}


.blog .tag-box.block .tag-block[data-title]:hover:after,
.blog .article-box.widget .article-block[data-title]:hover:after {
    /*background: rgba(86, 90, 95, .9);*/


    content: attr(data-title);
    position: absolute;
    z-index: 20;
    white-space: nowrap;
    background: rgba(85,85,85, 0.9);
    padding: 4px 8px;
    color: #fff;
    font-size: 14px;
    line-height: 18px;
    margin-top: 2.8em;
}


.blog .tag-box .tag-block .count,
.blog .article-box.widget .article-block .time {
    font-size: 14px;
    float: right;
}


/*
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
 *
 * live code
 *
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
*/

.blog .live-code.overlay {
    width: 100%;
    position: fixed;
    left: 0;
    top: 108px;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0, 0.5);
    visibility: hidden;
    opacity: 0;

    text-align: center;

    z-index: 300;

    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -ms-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
}

.blog.read .live-code.overlay {
    top: 70px;
}

.blog .live-code.overlay > div {
    height: 50vh;
    width: 60vw;
    background-color: #fff;

    margin-top: calc(25vh - 70px);

    box-shadow: 0 3px 9px rgba(0,0,0,.5);
    border-radius: 4px;
    display: inline-block;

    position: relative;

    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}

.blog.read .live-code.overlay > div {
    margin-top: calc(25vh - 55px);
}


.blog .live-code.overlay.show {
    opacity: 1;
    visibility: visible;
}

.blog .live-code.overlay > div iframe {
    width: 100%;
    height: 100%;
    border: none;
}

.live-code > div > span.close {
    position: absolute;
    right: 10px;
    font-size: 25px;

    cursor: pointer;

    color: #ddd;

    user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;

    -webkit-transition: color 0.2s;
    -moz-transition: color 0.2s;
    -ms-transition: color 0.2s;
    -o-transition: color 0.2s;
    transition: color 0.2s;
}

.live-code > div > span.close:hover {
    color: #333;
}



