@media screen and (max-width: 800px){

    .panel {
        border: none;
        margin-bottom: 0;
    }

    .blog {
        max-width: 100%;
    }
    .blog .header-panel .blog-name {
        margin-left: 10px;
        font-size: 1em;

    }

    .blog .header-panel .menus {
        font-size: 1.2em;
    }

    .blog .widget-panel {
        display: none;
    }

    .blog .wrapper {
        width: 100%;
        margin-left: 0;
        float: none;
        margin-top: 120px;
    }

    .blog .wrapper > div:first-child {
        margin-left: 0;
    }

    .blog.read .wrapper {
        transform: none;
    }

    .blog .to-top-box {
        right: 10px !important;
        opacity: 0.8;
        z-index: 10 !important;
    }

    .blog .wrapper .mark-down-menu {
        max-width: initial;
    }

    .blog .mark-down-content em.lang-type {
        font-size: 18px;
        top: 5px;
        right: 5px;
    }
}

@-moz-document url-prefix() {
    .mark-down-content table img {
        width: 100%;
    }
}


